import { Base, Type, markType } from "igniteui-core/type";
import { SizeData } from "igniteui-core/SizeData";
import { PointData } from "igniteui-core/PointData";
import { LabelAppearanceData } from "igniteui-core/LabelAppearanceData";
import { RectData } from "igniteui-core/RectData";

/**
 * @hidden 
 */
export class LinearGraphScaleLabelVisualData extends Base {
	static $t: Type = markType(LinearGraphScaleLabelVisualData, 'LinearGraphScaleLabelVisualData');
	private _labelValue: any = null;
	get labelValue(): any {
		return this._labelValue;
	}
	set labelValue(a: any) {
		this._labelValue = a;
	}
	private _labelSize: SizeData = null;
	get labelSize(): SizeData {
		return this._labelSize;
	}
	set labelSize(a: SizeData) {
		this._labelSize = a;
	}
	private _labelPosition: PointData = null;
	get labelPosition(): PointData {
		return this._labelPosition;
	}
	set labelPosition(a: PointData) {
		this._labelPosition = a;
	}
	private _appearance: LabelAppearanceData = null;
	get appearance(): LabelAppearanceData {
		return this._appearance;
	}
	set appearance(a: LabelAppearanceData) {
		this._appearance = a;
	}
	scaleByViewport(a: RectData): void {
		this.labelPosition = new PointData((this.labelPosition.x - a.left) / a.width, (this.labelPosition.y - a.top) / a.height);
		this.labelSize = new SizeData((this.labelSize.width) / a.width, (this.labelSize.height) / a.height);
	}
	serialize(): string {
		let a: string = this.labelValue.toString();
		let b: string = "{ labelValue: '" + a;
		b += "', labelSize: { width: " + this.labelSize.width + ", height: " + this.labelSize.height;
		b += "}, labelPosition: { x: " + this.labelPosition.x + ", y: " + this.labelPosition.y;
		b += "}, appearance: " + this.appearance.serialize() + "}";
		return b;
	}
}


